import React from 'react'
import { Outlet, Link } from 'react-router-dom'

import { Container, Col, Row, Card, DropdownButton , Dropdown} from 'react-bootstrap'

import logo from '../asset/img/logo.jpg'

export default function Home() {
  //<Button as={Link} to='town' variant="primary"></Button>
//item,
  return (
    <Container>
      <Row md={4}>
        <Col>
          <Card border="light" className='county text-center' >
            <Card.Img variant="top" src={logo} />
            <Card.Body>
              <Card.Title></Card.Title>
              <Card.Text>
                109
              </Card.Text>

              <DropdownButton id="dropdown-basic-button" title="">
                <Dropdown.Item as={Link} to='town1'></Dropdown.Item>
                <Dropdown.Item as={Link} to='town2'></Dropdown.Item>
                <Dropdown.Item as={Link} to='town3'></Dropdown.Item>
                <Dropdown.Item as={Link} to='town4'></Dropdown.Item>
                <Dropdown.Item as={Link} to='town5'></Dropdown.Item>
                <Dropdown.Item as={Link} to='town6'></Dropdown.Item>
                <Dropdown.Item as={Link} to='town7'></Dropdown.Item>
                <Dropdown.Item as={Link} to='town8'></Dropdown.Item>
                <Dropdown.Item as={Link} to='town9'></Dropdown.Item>
                <Dropdown.Item as={Link} to='town10'></Dropdown.Item>
                <Dropdown.Item as={Link} to='town11'></Dropdown.Item>
                <Dropdown.Item as={Link} to='town12'></Dropdown.Item>
              </DropdownButton>

            </Card.Body>
          </Card>
        </Col>
        <Col md={8}>
          <Outlet />
        </Col>
      </Row>
    </Container>
  )
}